"use client";
import Protected from "@/components/Protected";
import { useState } from "react";

export default function AddProduct() {
    const [form, setForm] = useState({ title: "", shortDesc: "", fullDesc: "", price: "", image: "" });

    const handleChange = e => setForm({ ...form, [e.target.name]: e.target.value });
    const handleSubmit = async e => {
        e.preventDefault();
        await fetch("http://localhost:5000/api/products", {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify(form),
        });
        alert("Product added!");
        setForm({ title: "", shortDesc: "", fullDesc: "", price: "", image: "" });
    };

    return (
        <Protected>
            <div className="p-10 max-w-2xl mx-auto">
                <h1 className="text-2xl font-bold mb-4">Add Product</h1>
                <form className="space-y-4" onSubmit={handleSubmit}>
                    <input type="text" name="title" placeholder="Title" value={form.title} onChange={handleChange} className="border p-2 w-full" />
                    <input type="text" name="shortDesc" placeholder="Short Description" value={form.shortDesc} onChange={handleChange} className="border p-2 w-full" />
                    <textarea name="fullDesc" placeholder="Full Description" value={form.fullDesc} onChange={handleChange} className="border p-2 w-full" />
                    <input type="number" name="price" placeholder="Price" value={form.price} onChange={handleChange} className="border p-2 w-full" />
                    <input type="text" name="image" placeholder="Image URL" value={form.image} onChange={handleChange} className="border p-2 w-full" />
                    <button type="submit" className="px-4 py-2 bg-green-600 text-white rounded hover:bg-green-700">Submit</button>
                </form>
            </div>
        </Protected>
    );
}
